* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0 auto !important;
  position: relative;
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  background-color: #f3f3f3;
  min-width: 320px;
  max-width: 1920px;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

a, a:hover {
  text-decoration: none;
}

.contenedor {
  margin: 1rem auto;
  padding: 1rem 0;
  max-width: 1920px;
  position: relative;
}

.header-home {
  background: transparent;
  position: absolute;
  width: 100%;
  z-index: 10;
}

.contenedor-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  gap: 1rem;
}

.iconos-header i {
  cursor: pointer;
  color: white;
  font-size: 2.2rem;
}

.contact-phone {
  font-weight: 700;
  padding: 0.6rem 1rem;
  border: 1px solid;
  border-radius: 5px;
  color: #fff;
}
.contact-phone:hover {
  background-color: #fff;
  border: 0;
}

.user-account-icon {
  display: none;
}

.tablet {
  display: none;
}

nav.desktop {
  display: none;
}
nav.desktop ul {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1rem;
  width: 70%;
}
nav.desktop li {
  padding: 3px 12px;
  opacity: 0.8;
  transition: all 0.2s;
}
nav.desktop a {
  color: #fff;
  opacity: 0.8;
}
nav.desktop a:hover {
  opacity: 1;
}

nav.desktop ul li:nth-last-child(1) a {
  padding: 3px 12px;
  background-color: dodgerblue;
  border-radius: 6px 0 6px 0;
  color: #fff;
}

@media (min-width: 769px) {
  .header-home {
    max-width: 1920px;
    width: 100%;
    z-index: 10;
    position: absolute;
  }

  .contenedor-header {
    width: 90%;
    margin: 0 auto;
  }

  .logo .logo-mobile {
    display: none;
  }

  .logo .tablet {
    display: block;
    width: 160px;
  }

  .iconos-header {
    gap: 4.5rem;
  }

  .user-account-icon {
    display: block;
  }
}
@media (min-width: 992px) {
  nav.desktop {
    display: flex;
    justify-content: center;
  }
}
.property-filter {
  border: 1px solid white;
  margin: 0 auto;
  background: linear-gradient(45deg, #14cfa6, #0040405c);
}
.property-filter h3 {
  color: #fff;
  padding: 1rem;
  text-align: center;
}

.search-form {
  display: flex;
  flex-direction: column;
}
.search-form button {
  padding: 2rem 0;
  width: 100%;
  border: 0;
  background-color: #6495eda8;
  color: #fff;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.search--form-field {
  padding: 2rem 1rem;
  border-top: 1px solid #80808047;
  margin-bottom: 5px;
}
.search--form-field:last-child {
  border-bottom: 1px solid #80808047;
}
.search--form-field label {
  display: block;
  font-weight: 700;
  color: #ede7e7;
}
.search--form-field select {
  padding: 1rem;
  width: 100%;
  border: 0;
  border-radius: 5px;
  background: transparent;
  color: #ede7e7;
}

@media (min-width: 769px) {
  .property-filter {
    background: linear-gradient(45deg, #000, transparent);
    border: 0;
    top: 350px;
    width: 80%;
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }

  .search-form {
    flex-direction: row;
    justify-content: space-around;
  }
  .search-form button {
    padding: 2rem 1rem;
    width: auto;
  }
  .search-form .search--form-field {
    padding: 0 1rem;
    border: 0;
  }
  .search-form .search--form-field option {
    color: #000;
  }
}
@media (min-width: 481px) {
  .most-featured-properties {
    grid-template-columns: repeat(2, 1fr);
  }
  .most-featured-properties .first {
    grid-column: 1/3;
  }
}
@media (min-width: 769px) {
  .most-featured-properties {
    grid-template-columns: repeat(3, 1fr);
  }
  .most-featured-properties .first {
    grid-column: 1/2;
  }

  .banner-home1 span {
    width: 70%;
    font-size: 3rem;
    font-weight: bold;
  }
}
.slide {
  background: linear-gradient(45deg, black, transparent), url("../images/slide-one.jpg");
  overflow: hidden;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.banner-home1 {
  background: linear-gradient(45deg, black, transparent), url("../images/banner1.webp");
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 480px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.banner-home1 span {
  padding: 0 3rem;
  color: #fff;
  text-align: center;
}

.most-featured-properties {
  display: grid;
  gap: 1rem;
  padding: 2rem 0;
}

.property {
  position: relative;
  background-color: #fff;
  box-shadow: -1px 1px 8px 0px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  padding-bottom: 1rem;
  overflow: hidden;
  cursor: pointer;
}
.property h3 {
  text-align: center;
  font-weight: 700;
  padding: 1rem 0;
}
.property .ref {
  margin: 0;
  margin-left: 10px;
  color: grey;
}
.property .address {
  padding: 0.5rem 1rem;
  text-decoration: underline;
  color: #0073bd;
}
.property .info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}
.property .info .price {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
  color: #0073bd;
  font-weight: 700;
}
.property .info .price .consultarPrecio {
  font-size: 1.5rem;
  line-height: 15px;
}
.property .info .price .fa-whatsapp {
  font-size: 2.5rem;
}
.property .operationType {
  position: absolute;
  border-radius: 3px;
  color: #fff;
  font-weight: 700;
  padding: 0 0.2rem;
  left: 5px;
  bottom: 18px;
  display: inline-table;
  z-index: 9999;
  font-size: 1.5rem;
}
.property .propertyType {
  position: absolute;
  bottom: 0;
  left: 5px;
  z-index: 9999;
  color: #fff;
  font-weight: 700;
  font-size: 1.2rem;
}
.property .fa-bookmark {
  position: absolute;
  right: 10px;
  top: 0;
  z-index: 110;
  color: #ffc100;
  font-size: 3rem;
}

.features {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
.features .feature {
  display: flex;
  gap: 0.2rem;
}
.features .feature p {
  font-weight: 100;
}
.features .feature i {
  font-size: 2.5rem;
  color: #000000a1;
}

.property-image {
  position: relative;
  transition: all 0.3s;
  overflow: hidden;
}
.property-image .gradient {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 100;
  background: linear-gradient(179deg, #00000099, #00000026);
}
.property-image img {
  width: 100%;
  transition: all 0.5s;
}
.property-image:hover img {
  transform: scale(1.1);
  overflow: hidden;
}

@media (min-width: 769px) {
  .property h3 {
    text-align: left;
    margin-left: 1rem;
  }
  .property .propertyType, .property .operationType {
    font-size: 1.4rem;
  }
}
.tag-alquiler {
  background-color: #ff3c01ed;
}

.tag-venta {
  background-color: #0297dfed;
}

.financiacion {
  letter-spacing: 0.5px;
  font-size: 1rem;
  padding: 0.2rem 0.5rem;
  margin-right: 5px;
  background-color: #ff0000e6;
  color: #fff;
  border-radius: 10px;
  text-align: center;
}

.lista-propiedades {
  margin-top: 2rem;
  margin-bottom: 5rem;
  padding: 0.3rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
}

@media (min-width: 769px) {
  .lista-propiedades {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 992px) {
  .lista-propiedades {
    grid-template-columns: repeat(4, 1fr);
    gap: 1.2rem;
  }
}
.form-createProduct fieldset {
  border: 1px solid #8080805c;
}
.form-createProduct legend {
  display: inline;
  width: auto;
  padding: 0 1rem !important;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.price-fields {
  display: flex;
  align-items: end;
  gap: 1rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 30%);
  gap: 2rem;
}

.label-form {
  padding: 0 1rem;
}
.label-form input {
  margin-right: 1rem;
}

.property-detail {
  padding: 120px 0;
}
.property-detail h1 {
  font-weight: bold;
  padding: 1rem;
}
.property-detail h2 {
  text-align: center;
  text-transform: uppercase;
  font-weight: 100;
}
.property-detail .buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
}
.property-detail .buttons button {
  border: 1px solid gray;
  padding: 0.2rem 1rem;
  border-radius: 5px;
  background-color: #7b00b9;
  color: white;
  box-shadow: 1px 0px 7px 4px #00000052;
}
.property-detail .informacion {
  grid-column: 2/3;
  grid-row: 1/2;
}
.property-detail .informacion div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #8080804d;
  padding: 0.2rem 0.5rem;
}
.property-detail .informacion div p {
  margin: 0;
  padding: 0;
}
.property-detail .informacion span {
  font-weight: bold;
}
.property-detail .informacion .badge-detail {
  font-size: 1rem;
  letter-spacing: 0.5px;
  margin-right: 5px;
  padding: 0.2rem 0.5rem;
  text-align: center;
  text-transform: uppercase;
}
.property-detail .property-detail--info .descripcion {
  padding: 1rem;
}
.property-detail .features-container {
  border: 1px solid #80808085;
  border-radius: 5px;
  padding: 1rem 0.5rem;
}
.property-detail .propertyFeatures div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 769px) {
  .property-detail {
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .property-detail .buttons {
    display: none;
  }
  .property-detail .property-detail--title {
    grid-column: 2/3;
    grid-row: 1/2;
    font-size: 3rem;
  }
  .property-detail .informacion {
    margin-top: 6rem;
  }
}
.footer {
  margin-top: 10rem;
  height: 221px;
  background-color: #3586ff;
  text-align: center;
  position: relative;
}
.footer .wave1 {
  background-image: url(/images/wave.png);
  height: 100px;
  position: absolute;
  top: -96px;
  left: 0;
  width: 100%;
}
.footer .social-icons {
  display: flex;
  gap: 1.6rem;
  justify-content: center;
  padding: 30px;
  font-size: 3rem;
}
.footer .social-icons a {
  color: #fff;
  transition: all 0.5s;
}
.footer .social-icons a:hover {
  transform: translateY(-10px);
}
.footer .nav-footer ul {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.footer .nav-footer ul li a {
  color: #fff;
  opacity: 0.75;
}
.footer .nav-footer ul li a:hover {
  opacity: 1;
}
.footer .copy {
  color: #fff;
}

@media (min-width: 769px) {
  .nav-footer ul {
    display: flex;
  }
}